body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
}

.map{
  width: 100vw;
  height: 100vh;
  position: relative;
}

.location-icon{
  font-size: 2rem;
  color: red;
}

.loader{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.loader img{
  width: 400px;
}

.loader h1{
  margin-top: -100px;
}

.location-info{
  position: absolute;
  top: 50px;
  right: 50px;
  width: 400px;
  min-height: 200px;
  padding: 20px;
  background-color: rgba(0, 0, 0, 0.6);
  border-radius: 10px;
  font-size: 18px;
  color: #efefef
  ;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
}

.location-info ul{
  list-style-type: none;
  padding: 0;
}

.location-info li{
  padding:  5px 0 ;
}